
import Vue from "vue";
import { Component } from "vue-property-decorator";

@Component
export default class ToastNotification extends Vue {
  message = "";
  callbackText = "";
  visible = false;
  timeout: number | null = null;
  callback: (() => void) | null = null;

  created() {
    this.$root.$on("toast", this.showToast);
  }

  showToast({
    message = "",
    callbackText = "",
    duration = 3000,
    callback = null
  }: {
    message?: string;
    callbackText?: string;
    duration?: number;
    callback?: (() => void) | null;
  }) {
    this.message = message;
    this.callbackText = callbackText;
    this.visible = true;
    this.callback = callback;

    if (this.timeout) {
      clearTimeout(this.timeout);
    }

    this.timeout = window.setTimeout(() => {
      this.visible = false;
    }, duration);
  }

  handleButtonClick() {
    this.visible = false;
    if (this.callback && typeof this.callback === "function") {
      this.callback();
    }
  }
}
