
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";

@Component
export default class BrowserUIPreview extends Vue {
  @Prop({
    default: "desktop"
  })
  breakpoint!:
    | "mobile"
    | "desktop"
    | "breakpoint_base"
    | "breakpoint_phone_portrait";

  get isDesktop() {
    return (
      this.breakpoint === "desktop" || this.breakpoint === "breakpoint_base"
    );
  }

  get isMobile() {
    return (
      this.breakpoint === "mobile" ||
      this.breakpoint === "breakpoint_phone_portrait"
    );
  }
}
