
import Vue from "vue";
import { Component, Prop, Watch } from "vue-property-decorator";
import OnboardingLayout from "../components/OnboardingLayout.vue";
import Stepper from "../components/Stepper.vue";
import BottomNav from "../components/BottomNav.vue";
import { onboardingStore } from "@/store/store";
import OnboardingIframe from "../components/OnboardingIframe.vue";
import LogoBeta from "../components/LogoBeta.vue";
import ToggleBreakpoints from "../components/ToggleBreakpoints.vue";
import { RouteConfig } from "vue-router";
import OnboardingFeedback from "../components/OnboardingFeedback.vue";
import OnboardingInsertSectionSelect from "../components/OnboardingInsertSectionSelect.vue";
import { OnboardingGeneratedPage } from "../types";

@Component({
  components: {
    OnboardingLayout,
    Stepper,
    BottomNav,
    LogoBeta,
    OnboardingIframe,
    ToggleBreakpoints,
    OnboardingFeedback,
    OnboardingInsertSectionSelect
  }
})
export default class OnboardingTemplatePages extends Vue {
  currentPostId: number | null = null;

  @Prop()
  routes!: RouteConfig[];

  get pages() {
    return onboardingStore.generatedPages.pages;
  }

  @Watch("pages", { immediate: true })
  onPagesLoaded(newPages: OnboardingGeneratedPage[]) {
    if (newPages.length > 0 && !this.currentPostId) {
      this.currentPostId = newPages[0].postId;
    }
  }

  setCurrentPostId(postId: number) {
    this.currentPostId = postId;
  }
}
