
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import OnboardingLayout from "../../components/OnboardingLayout.vue";
import Stepper from "../../components/Stepper.vue";
import BottomNav from "../../components/BottomNav.vue";
import Button from "../../components/Button.vue";
import SampleSection from "../../components/SampleSection.vue";
import { onboardingStore } from "@/store/store";
import LogoBeta from "../../components/LogoBeta.vue";
import ManageButtons from "../../components/ManageButtons.vue";
import ManagePalettes from "../../components/ManagePalettes.vue";
import ManageTypography from "../../components/ManageTypography.vue";
import OnboardingIframe from "../../components/OnboardingIframe.vue";
import ToggleBreakpoints from "../../components/ToggleBreakpoints.vue";
import { RouteConfig } from "vue-router";
import OnboardingFeedback from "../../components/OnboardingFeedback.vue";

@Component({
  components: {
    OnboardingLayout,
    Stepper,
    Button,
    BottomNav,
    SampleSection,
    LogoBeta,
    ManageButtons,
    ManagePalettes,
    ManageTypography,
    OnboardingIframe,
    ToggleBreakpoints,
    OnboardingFeedback
  }
})
export default class OnboardingFoundationStyleguide extends Vue {
  @Prop()
  routes!: RouteConfig[];

  get colorPalettes() {
    return [
      onboardingStore.websiteUI.colorPalettes.primary,
      ...onboardingStore.websiteUI.colorPalettes.secondary
    ];
  }

  get isHomepageCreated() {
    return onboardingStore.isHomepageCreated;
  }

  get homepage() {
    return onboardingStore.generatedPages.homepage;
  }

  get pages() {
    return onboardingStore.generatedPages.pages;
  }
}
