
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";

@Component({
  model: {
    prop: "value",
    event: "input"
  }
})
export default class TextInput extends Vue {
  @Prop()
  value?: string;

  @Prop()
  id?: string;

  @Prop()
  placeholder?: string;

  @Prop()
  label?: string;

  @Prop()
  note?: string;

  @Prop({ default: "input" })
  type!: "input" | "textarea";

  update(event: Event) {
    this.$emit("input", (event.target as HTMLInputElement).value);
  }
}
