
import Vue from "vue";
import { Component, Watch } from "vue-property-decorator";
import OnboardingAppLayout from "./components/OnboardingAppLayout.vue";
import { logEvent } from "@/util/tracking";

@Component({
  components: {
    OnboardingAppLayout
  }
})
export default class OnboardingApp extends Vue {
  mounted() {
    logEvent("Onboarding", {
      action: "onboarding_app_loaded",
      url: location.href,
      title: document.title,
      viewport: {
        width: window.innerWidth,
        height: window.innerHeight
      }
    });
  }

  @Watch("$route", { immediate: true })
  onRouteChange(newRoute: any) {
    logEvent("Onboarding", {
      action: "onboarding_path_changed",
      pathName: newRoute.name || ""
    });
  }
}
