
import Vue from "vue";
import { Component, Watch } from "vue-property-decorator";
import Logo from "../components/Logo.vue";
import Button from "../components/Button.vue";
import clipboardCopy from "clipboard-copy";
import { configStore, onboardingStore } from "@/store/store";
import { fireConfetti } from "../lib/confetti";
import {
  trashOnboardingPagesWordpress,
  removeHomepageSectionsWithBreakdance
} from "../lib/pages";
import OnboardingLoader from "../components/OnboardingLoader.vue";
import OnboardingFeedback from "../components/OnboardingFeedback.vue";
import { logEvent } from "@/util/tracking";

@Component({
  components: {
    Logo,
    Button,
    OnboardingLoader,
    OnboardingFeedback
  }
})
export default class OnboardingComplete extends Vue {
  arePagesTrashed = false;
  isSyncingComplete = false;

  get websiteUrl() {
    return configStore.neededData?.homeUrl || "";
  }

  get homepageId() {
    return onboardingStore.generatedPages.homepage?.postId;
  }

  openBuilder() {
    logEvent("Onboarding", {
      action: "onboarding_app_ui_interaction",
      type: "onboarding_complete_open_builder"
    });

    const url = `${configStore.neededData?.homeUrl}/?breakdance=builder&id=${this.homepageId}`;
    window.open(url, "_blank");
  }

  get dataToRemove() {
    return onboardingStore.generatedPages.toRemove;
  }

  get selectedPagesToTrash() {
    return onboardingStore.generatedPages.toRemove.pageIds;
  }

  get homepageSectionsToTrash() {
    return onboardingStore.generatedPages.toRemove.homepageSectionIds;
  }

  get homepage() {
    return onboardingStore.generatedPages.homepage;
  }

  open() {
    window.open(this.websiteUrl, "_blank");
  }

  async syncSelectedPages() {
    if (
      this.isSyncingComplete ||
      this.selectedPagesToTrash?.length == 0 ||
      !this.selectedPagesToTrash
    )
      return;
    return await trashOnboardingPagesWordpress(this.selectedPagesToTrash);
  }

  async syncSelectedSections() {
    if (
      this.isSyncingComplete ||
      this.homepageSectionsToTrash?.length == 0 ||
      !this.homepageSectionsToTrash ||
      !this.homepage?.postId
    )
      return;

    return await removeHomepageSectionsWithBreakdance(
      this.homepage.postId,
      this.homepageSectionsToTrash
    );
  }

  async syncOnboardingData() {
    const pages = await this.syncSelectedPages();
    const sections = await this.syncSelectedSections();

    return Promise.all([pages, sections]);
  }

  copy() {
    clipboardCopy(this.websiteUrl);
    fireConfetti();
    this.$root.$emit("toast", {
      message: "Copied!",
      duration: 3000
    });
  }

  @Watch("dataToRemove", { immediate: true })
  async onDataToRemoveChange() {
    await this.syncOnboardingData();
    this.isSyncingComplete = true;
    fireConfetti();
  }
}
