
import Vue from "vue";
import { Component } from "vue-property-decorator";
import { getDesignSetBarebonesData } from "../lib/pages";
import { ExportedTreeV, TreeNode } from "@/types/tree";
import { isRight } from "fp-ts/lib/Either";
import { documentStore, uiStore } from "@/store/store-accessor";
import OnboardingInsertSectionDropdown from "./OnboardingInsertSectionDropdown.vue";
import { RemoteDesignProviderPost } from "../types";

@Component({
  components: {
    OnboardingInsertSectionDropdown
  }
})
export default class OnboardingInsertSectionSelect extends Vue {
  designProviderPosts: RemoteDesignProviderPost[] = [];
  newSections: TreeNode[] = [];
  isLoading = false;

  get sections() {
    return documentStore.document.tree.root.children;
  }

  get activeElementId() {
    return uiStore.activeElementId;
  }

  async fetchBarebonesSections() {
    this.isLoading = true;
    const designSetBarebonesData = await getDesignSetBarebonesData();

    if (!designSetBarebonesData) return false;

    const postsWithValidTree = designSetBarebonesData.posts.filter(post => {
      return !!post.tree && isRight(ExportedTreeV.decode(post.tree));
    }) as RemoteDesignProviderPost[];

    this.designProviderPosts = postsWithValidTree;
    this.isLoading = false;
    return designSetBarebonesData;
  }

  mounted() {
    this.fetchBarebonesSections();
  }
}
