
import Vue from "vue";
import { Component } from "vue-property-decorator";
import { Font } from "@/types/fonts";

import { Prop } from "vue-property-decorator";
import TypographyPickerOverlay from "./TypographyPickerOverlay.vue";
import TypographyFont from "./TypographyFont.vue";

@Component({
  components: {
    TypographyPickerOverlay,
    TypographyFont
  }
})
export default class TypographyPicker extends Vue {
  isOverlayOpen = false;
  @Prop()
  value?: Font;

  @Prop({ default: "Body" })
  label!: string;

  @Prop()
  isOpen!: boolean;

  update(font: Font) {
    this.isOverlayOpen = false;
    this.$emit("update", font);
  }

  toggle() {
    this.$emit("open", !this.isOpen);
  }
}
