import { onboardingStore, globalStore } from "@/store/store";
import { fpJsonStringify } from "@/util/fp-json-stringify";
import { OnboardingDataV, OnboardingData } from "../types";
import { makeWordPressAjaxRequest } from "@/lib/wordpress/ajax";
import { getWebsiteCategoryGoalsWritingStyleAsQualifier } from "../lib/ai";
import { pipe } from "fp-ts/lib/function";
import { fold } from "fp-ts/lib/Either";
import defensiveCopy from "@/util/defensive-copy";
import { saveAiSettings } from "@/lib/ai/api";

export async function saveOnboardingSettings() {
  const settings = onboardingStore.onboardingData;

  const request = await makeWordPressAjaxRequest({
    action: "breakdance_save_onboarding_settings",
    dataToPost: {
      settings: fpJsonStringify(settings)
    },
    decoder: OnboardingDataV
  });

  return pipe(
    request,
    fold(
      () => false,
      loadedData => {
        onboardingStore.setOnboardingData(loadedData);
        saveOnboardingSettingsAsAISettingsPrompt(loadedData);
        return true;
      }
    )
  );
}

export async function saveOnboardingSettingsAsAISettingsPrompt(
  loadedOnboardingData: OnboardingData
) {
  const aiSettings = defensiveCopy(globalStore.aiSettings);

  const { websiteName, websiteDescription } =
    loadedOnboardingData.websiteDetails;

  aiSettings.prompt.businessName = websiteName;
  aiSettings.prompt.businessDescription = websiteDescription;
  aiSettings.prompt.qualifiers = getWebsiteCategoryGoalsWritingStyleAsQualifier(
    loadedOnboardingData.websiteDetails
  );

  globalStore.setAiSettings(aiSettings);
  await saveAiSettings();
}

export async function getOnboardingSettings() {
  const request = await makeWordPressAjaxRequest({
    action: "breakdance_get_onboarding_settings",
    decoder: OnboardingDataV,
    suppressErrorLogging: true
  });

  return pipe(
    request,
    fold(
      () => false,
      loadedData => {
        onboardingStore.setOnboardingData(loadedData);
        return true;
      }
    )
  );
}
