
import Vue from "vue";
import Component from "vue-class-component";
import { BorderRadius, BORDER_RADIUS_VALUES } from "../types";
import { onboardingStore } from "@/store/store";
import Button from "./Button.vue";
import DropdownSection from "./DropdownSection.vue";
import { logEvent } from "@/util/tracking";

@Component({
  components: {
    Button,
    DropdownSection
  }
})
export default class ManageButtons extends Vue {
  borderRadius: BorderRadius = onboardingStore.websiteUI.buttons.radius;

  corners: BorderRadius[] = ["none", "sm", "md", "lg", "full"];

  get roundedCorners() {
    return onboardingStore.websiteUI.buttons.radius;
  }

  get primaryPalette() {
    return onboardingStore.websiteUI.colorPalettes.primary;
  }

  get neutralPalette() {
    return onboardingStore.websiteUI.colorPalettes.neutral;
  }

  get borderRadiusTopLeftStyle() {
    return {
      borderTopLeftRadius: BORDER_RADIUS_VALUES[this.borderRadius]?.style
    };
  }

  get stylesPrimary() {
    if (!this.primaryPalette) {
      return {};
    }
    return {
      backgroundColor: this.primaryPalette.base.value?.toString(),
      color: this.primaryPalette.text.value?.toString(),
      borderRadius: BORDER_RADIUS_VALUES[this.borderRadius]?.style
    };
  }

  get stylesSecondary() {
    if (!this.neutralPalette) {
      return {};
    }
    return {
      backgroundColor: "transparent",
      text: this.neutralPalette.text.value?.toString(),
      borderColor: this.neutralPalette.scale[300].value?.toString(),
      borderRadius: BORDER_RADIUS_VALUES[this.borderRadius]?.style
    };
  }

  get stylesForms() {
    if (!this.neutralPalette) {
      return {};
    }
    return {
      borderColor: this.neutralPalette.scale[200].value?.toString(),
      borderRadius: BORDER_RADIUS_VALUES[this.borderRadius]?.style
    };
  }

  rotateCorners() {
    logEvent("Onboarding", {
      action: "onboarding_app_ui_interaction",
      type: "manage_buttons_rotate_corners"
    });
    const currentIndex = this.corners.indexOf(this.borderRadius);
    const nextIndex = (currentIndex + 1) % this.corners.length;
    this.borderRadius = this.corners[nextIndex];
    onboardingStore.updateButtons({ radius: this.borderRadius });
  }
}
