
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { RouteConfig } from "vue-router";
import Button from "./Button.vue";
import { getOnboardingSettings, saveOnboardingSettings } from "../lib/settings";
import { configStore } from "@/store/store-accessor";
import { fetchAiSettings } from "@/lib/ai/api";
import iframeManager from "../lib/iframeManager";

@Component({
  components: {
    Button
  }
})
export default class BottomNav extends Vue {
  @Prop()
  routes!: RouteConfig[];

  @Prop({
    default: true
  })
  isValidated!: boolean;

  @Prop({
    type: Function,
    default: null
  })
  onNextCallback!: (() => Promise<void>) | null;

  loading = false;

  get currentRouteIndex() {
    return this.routes.findIndex(route => route.path === this.$route.path);
  }

  get loginURL(): string | undefined {
    return configStore.neededData?.adminLoginUrl;
  }

  get currentRoute() {
    return this.routes[this.currentRouteIndex];
  }

  async next() {
    if (this.loading) return;

    this.loading = true;

    try {
      if (this.onNextCallback) {
        await this.onNextCallback();
      }

      await saveOnboardingSettings();

      await iframeManager.sendRequest({
        payload: {
          action: "saveDocumentToWordPress"
        }
      });

      if (this.isLastRoute) {
        this.$router.push("/");
      } else {
        if (this.currentRoute.meta?.nextPath) {
          this.$router.push(this.currentRoute.meta.nextPath);
        } else {
          this.$router.push(this.routes[this.currentRouteIndex + 1].path);
        }
      }
    } catch (error) {
      console.error("Error in onNextCallback:", error);
    } finally {
      this.loading = false;
    }
  }

  prev() {
    if (this.currentRouteIndex > 0) {
      this.$router.push(this.routes[this.currentRouteIndex - 1].path);
    }
  }

  get isFirstRoute() {
    return this.currentRouteIndex === 0;
  }

  get isLastRoute() {
    return (
      this.currentRouteIndex === this.routes.length - 1 &&
      this.currentRoute.meta?.nextPath === undefined
    );
  }

  async beforeMount() {
    Promise.all([getOnboardingSettings(), fetchAiSettings()]);
  }
}
