
import Vue from "vue";
import { Component } from "vue-property-decorator";
import LogoBeta from "../components/LogoBeta.vue";
import Button from "../components/Button.vue";
import OnboardingLayout from "../components/OnboardingLayout.vue";
import OnboardingIframe from "../components/OnboardingIframe.vue";
import BottomNav from "../components/BottomNav.vue";
import Stepper from "../components/Stepper.vue";
import OnboardingImportDesignProvider from "../components/OnboardingImportDesignProvider.vue";
import { PATHS } from "../router";
import { onboardingStore } from "@/store/store";
import { routesBarebones, routesTemplates } from "../router";
import { saveOnboardingSettings } from "../lib/settings";
import { logEvent } from "@/util/tracking";

@Component({
  components: {
    LogoBeta,
    Button,
    OnboardingLayout,
    OnboardingIframe,
    BottomNav,
    Stepper,
    OnboardingImportDesignProvider
  }
})
export default class OnboardingTemplateLibrary extends Vue {
  homepageId: null | number = null;

  get selectedDesignLibraryProvider() {
    return onboardingStore.designLibrary.selectedProvider;
  }

  get routes() {
    const redirectPaths = {
      [PATHS.BAREBONES_IMPORT]: routesBarebones,
      [PATHS.TEMPLATE_IMPORT]: routesTemplates
    };

    return redirectPaths[this.$route.path] || [];
  }

  async redirect() {
    const redirectPaths = {
      [PATHS.BAREBONES_IMPORT]: PATHS.BAREBONES_STYLEGUIDE,
      [PATHS.TEMPLATE_IMPORT]: PATHS.TEMPLATE_HOMEPAGE
    };

    const newPath = redirectPaths[this.$route.path];
    if (newPath) {
      await saveOnboardingSettings();
      this.$router.push(newPath);
    }
  }
}
