
import Vue from "vue";
import Component from "vue-class-component";
import Button from "./Button.vue";
import PaletteGenerator from "./PaletteGenerator.vue";
import { onboardingStore } from "../../../store/store";
import Icon from "@/components/UI/Icon.vue";
import { NamedColorPalette } from "../types";
import DropdownSection from "./DropdownSection.vue";
import { logEvent } from "@/util/tracking";

@Component({
  components: {
    Button,
    PaletteGenerator,
    Icon,
    DropdownSection
  }
})
export default class ManagePalettes extends Vue {
  maxSecondaryPalettes = 4;

  get primaryPalette() {
    return onboardingStore.websiteUI.colorPalettes.primary;
  }

  get secondaryPalettes() {
    return onboardingStore.websiteUI.colorPalettes.secondary;
  }

  get neutralPalette() {
    return onboardingStore.websiteUI.colorPalettes.neutral;
  }

  get isSecondaryPaletteFull() {
    return onboardingStore.isSecondaryPalettesLimitReached;
  }

  addPalette() {
    logEvent("Onboarding", {
      action: "onboarding_app_ui_interaction",
      type: "manage_buttons_add_palette"
    });
    if (!this.primaryPalette) {
      onboardingStore.createPrimaryPalette();
    } else {
      onboardingStore.addToSecondaryPalettes();
    }
  }

  shufflePalettes() {
    onboardingStore.shufflePalettes();
    logEvent("Onboarding", {
      action: "onboarding_app_ui_interaction",
      type: "manage_buttons_shuffle_palettes"
    });
  }

  removeSecondaryPalette(index: number) {
    onboardingStore.removeFromSecondaryPalettes(index);
  }

  updateSecondaryPalette(index: number, palette: NamedColorPalette) {
    onboardingStore.updateSecondaryPalette({ index, palette });
  }

  updatePrimaryPalette(palette: NamedColorPalette) {
    onboardingStore.updatePrimaryPalette(palette);
  }
}
