
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { DesignProvider } from "@/types/design-library";
import { onboardingStore } from "@/store/store";
import Button from "./Button.vue";
import ButtonIcon from "./ButtonIcon.vue";
import Icon from "@/components/UI/Icon.vue";
import BrowserUIPreview from "./BrowserUIPreview.vue";
import { logEvent } from "@/util/tracking";

@Component({
  components: {
    Button,
    Icon,
    ButtonIcon,
    BrowserUIPreview
  }
})
export default class OnboardingDesignProvider extends Vue {
  isOverlayOpen = false;
  preview: "mobile" | "desktop" = "desktop";
  internalProvider: DesignProvider | null = null;

  @Prop({ required: true })
  provider!: DesignProvider;

  get providerName(): string {
    // Use internal provider instead of prop
    return this.internalProvider?.name || this.provider.name;
  }

  get providerUrl(): string {
    return this.internalProvider?.url || this.provider.url;
  }

  get providerThumbnail(): string | undefined {
    return (
      this.internalProvider?.meta?.thumbnailUrl ||
      this.provider.meta?.thumbnailUrl
    );
  }

  update() {
    logEvent("Onboarding", {
      ction: "onboarding_app_template_imported",
      provider: this.internalProvider?.name || this.provider.name,
      type: this.internalProvider?.type || this.provider.type
    });

    this.$emit("update", this.internalProvider);
    this.isOverlayOpen = false;
  }

  setPreview(preview: "mobile" | "desktop") {
    this.preview = preview;
  }

  get providers() {
    return onboardingStore.designLibrary.data.designProviders;
  }

  created() {
    this.internalProvider = this.provider;

    logEvent("Onboarding", {
      action: "onboarding_app_template_preview_open",
      provider: this.internalProvider?.name || this.provider.name,
      type: this.internalProvider?.type || this.provider.type
    });
  }

  nextProvider() {
    const currentIndex = this.providers.findIndex(
      provider =>
        provider.name === (this.internalProvider?.name || this.provider.name) &&
        provider.type === this.provider.type
    );
    const nextIndex = (currentIndex + 1) % this.providers.length;
    this.internalProvider = this.providers[nextIndex];
  }

  previousProvider() {
    const currentIndex = this.providers.findIndex(
      provider =>
        provider.name === (this.internalProvider?.name || this.provider.name) &&
        provider.type === this.provider.type
    );
    const previousIndex =
      (currentIndex - 1 + this.providers.length) % this.providers.length;
    this.internalProvider = this.providers[previousIndex];
  }

  openPreviewInNewTab() {
    window.open(this.providerUrl, "_blank");
    logEvent("Onboarding", {
      action: "onboarding_app_template_preview_new_tab",
      provider: this.internalProvider?.name || this.provider.name,
      type: this.internalProvider?.type || this.provider.type
    });
  }
}
