
import Vue from "vue";
import { Component } from "vue-property-decorator";
import Logo from "../../components/Logo.vue";
import Button from "../../components/Button.vue";
import { PATHS } from "../../router";
import { pipe } from "fp-ts/function";
import { fold } from "fp-ts/Either";
import { makeWordPressAjaxRequest } from "@/lib/wordpress/ajax";
import { DesignLibraryDataV, DesignProvider } from "@/types/design-library";
import OnboardingDesignProvider from "../../components/OnboardingDesignProvider.vue";
import { onboardingStore } from "@/store/store";
import Icon from "@/components/UI/Icon.vue";
import { logEvent } from "@/util/tracking";
import OnboardingFeedback from "../../components/OnboardingFeedback.vue";

@Component({
  components: {
    Logo,
    Button,
    OnboardingDesignProvider,
    Icon,
    OnboardingFeedback
  }
})
export default class OnboardingTemplateLibrary extends Vue {
  pathBarebones: string = PATHS.BAREBONES;
  isLoading = true;
  currentFilter: string[] = ["complete_site", "sections"];

  update(provider: DesignProvider) {
    logEvent("Onboarding", {
      action: "onboarding_app_template_imported",
      provider: provider
    });
    onboardingStore.setDesignLibrarySelectedProvider(provider);
    this.$router.push(PATHS.TEMPLATE_IMPORT);
  }

  setFilter(filter: string[]) {
    this.currentFilter = filter;
  }

  async importProviders() {
    return pipe(
      await makeWordPressAjaxRequest({
        action: "breakdance_get_design_library_data",
        decoder: DesignLibraryDataV
      }),
      fold(
        () => {
          this.isLoading = false;
        },
        data => {
          onboardingStore.updateDesignLibraryData(data);
          this.isLoading = false;
        }
      )
    );
  }

  get providers() {
    return (
      onboardingStore.designLibrary.data?.designProviders.filter(provider => {
        if (this.currentFilter.length === 0) return true;
        return this.currentFilter.some(filter => {
          return provider?.type === filter;
        });
      }) || []
    );
  }

  goBackToCustomization() {
    this.$router.push(PATHS.CUSTOMIZATION_SELECT);
  }

  mounted() {
    this.importProviders();
  }
}
