
import Vue from "vue";

import { Component, Prop } from "vue-property-decorator";

import Icon from "@/components/UI/Icon.vue";

@Component({
  components: {
    Icon
  }
})
export default class ButtonIcon extends Vue {
  @Prop({ default: false })
  disabled!: boolean;

  @Prop({
    default: "default"
  })
  size!: "small" | "default" | "large";

  @Prop()
  icon!: string;

  @Prop()
  rotate?: string;

  onClick() {
    this.$emit("click");
  }

  get iconSize() {
    return this.size === "small" ? 16 : 20;
  }

  get classes() {
    return {
      "button-icon": true,
      "button-icon--disabled": this.disabled,
      "button-icon--small": this.size === "small",
      "button-icon--large": this.size === "large"
    };
  }
}
