
import Vue from "vue";
import Component from "vue-class-component";
import { Prop, Watch } from "vue-property-decorator";

import ButtonIcon from "./ButtonIcon.vue";
import PalettePicker from "./PalettePicker.vue";
import TextInput from "./TextInput.vue";
import { NamedColorPalette } from "../types";
import {
  generatePalette,
  inlineContainerStyle,
  randomColor
} from "../lib/color";
import Icon from "@/components/UI/Icon.vue";
import clipboardCopy from "clipboard-copy";

@Component({
  components: {
    ButtonIcon,
    PalettePicker,
    TextInput,
    Icon
  }
})
export default class PaletteGenerator extends Vue {
  @Prop({
    required: true
  })
  palette!: NamedColorPalette;

  internalPalette: NamedColorPalette = { ...this.palette };

  @Prop({
    default: false
  })
  show!: boolean;

  close() {
    this.$emit("close");
  }

  style(color: string) {
    return inlineContainerStyle(color, this.palette);
  }

  shuffle() {
    // generate new palette based on the current color
    const newPalette = generatePalette(randomColor());

    console.log("newPalette", newPalette);
    this.internalPalette = newPalette;

    this.palette = newPalette;
    // update the palette with the new colors
    this.$emit("update", {
      ...newPalette
    });
  }

  updateColor(color: string) {
    // Generate new palette based on the new color
    const newPalette = generatePalette(color);
    this.internalPalette = {
      ...this.internalPalette,
      base: newPalette.base,
      scale: newPalette.scale
    };
    // Emit the updated palette
    this.$emit("update", {
      ...this.internalPalette
    });
  }

  copyColor(color: string) {
    clipboardCopy(color);
    this.$root.$emit("toast", {
      message: "Copied!",
      duration: 3000
    });
  }

  updateName() {
    // Emit just the updated name of the palette
    this.$emit("update", {
      ...this.internalPalette,
      label: this.internalPalette.label
    });
  }

  mounted() {
    // Sync internal palette with the prop
    this.internalPalette = { ...this.palette };
  }

  @Watch("palette")
  onPaletteChange(newPalette: NamedColorPalette) {
    // Sync internal palette with the prop
    this.internalPalette = { ...newPalette };
  }
}
