
import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import { generatePalette } from "../lib/color";

@Component
export default class SampleSection extends Vue {
  @Prop({
    default: "#000000"
  })
  color!: string;

  get palette() {
    return generatePalette(this.color);
  }

  @Prop({
    default: "light"
  })
  theme!: "light" | "dark";

  internalTheme = this.theme;

  get style() {
    return {
      backgroundColor:
        this.internalTheme === "light"
          ? this.palette.scale[200]
          : this.palette.scale[900],
      color:
        this.internalTheme === "light"
          ? this.palette.scale[900]
          : this.palette.scale[300]
    };
  }

  get classes() {
    return {
      "is-dark": this.theme === "dark"
    };
  }

  toggleTheme() {
    this.internalTheme = this.internalTheme === "light" ? "dark" : "light";
  }
}
