
import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import { configStore } from "@/store/store";
import ButtonIcon from "./ButtonIcon.vue";
import TypographyFont from "./TypographyFont.vue";
import { Font } from "@/types/fonts";
import TypographyCategory from "./TypographyCategory.vue";

@Component({
  components: {
    ButtonIcon,
    TypographyFont,
    TypographyCategory
  }
})
export default class PaletteGenerator extends Vue {
  filter = "";

  @Prop()
  label!: string;

  @Prop({
    default: false
  })
  show!: boolean;

  get fontsByCategory() {
    return configStore.fonts.reduce((acc: any, font: Font) => {
      const category = font.category || "Other";
      if (!acc[category]) {
        acc[category] = [];
      }
      acc[category].push(font);
      return acc;
    }, {});
  }

  update(font: Font) {
    this.$emit("update", font);
  }

  close() {
    this.$emit("close");
  }
}
