
import Vue from "vue";
import Component from "vue-class-component";
import { isLightColor } from "../lib/color";
import { NamedColorPalette } from "../types";
import Button from "./Button.vue";
import { Prop } from "vue-property-decorator";
import Icon from "@/components/UI/Icon.vue";
import ButtonIcon from "./ButtonIcon.vue";
import PalettePicker from "./PalettePicker.vue";
import TextInput from "./TextInput.vue";
import PaletteOverlay from "./PaletteOverlay.vue";

@Component({
  components: {
    Button,
    ButtonIcon,
    Icon,
    PalettePicker,
    TextInput,
    PaletteOverlay
  }
})
export default class PaletteGenerator extends Vue {
  isOverlayOpen = false;

  @Prop()
  palette!: NamedColorPalette;

  @Prop({ default: false })
  neutral!: boolean;

  get textStyle() {
    if (!this.palette) return {};
    return {
      color: isLightColor(this.palette.base.value?.toString())
        ? this.palette.scale[900].value?.toString()
        : this.palette.scale[100].value?.toString()
    };
  }

  get primaryTagStyle() {
    if (!this.palette) return {};
    return {
      backgroundColor: isLightColor(this.palette.base.value?.toString())
        ? this.palette.scale[600].value?.toString()
        : this.palette.scale[600].value?.toString(),
      color: isLightColor(this.palette.base.value?.toString())
        ? this.palette.scale[900].value?.toString()
        : this.palette.scale[100].value?.toString()
    };
  }

  updatePalette(palette: NamedColorPalette) {
    this.$emit("update", palette);
  }

  removePalette() {
    this.$emit("remove");
  }

  lockPalette() {
    this.$emit("update", {
      ...this.palette,
      locked: !this.palette.locked
    });
  }

  get isLightColor() {
    if (!this.palette?.base) return false;
    return isLightColor(this.palette.base.value?.toString());
  }
}
