
import Vue from "vue";
import { Component, Watch } from "vue-property-decorator";
import { onboardingStore } from "@/store/store";
import { getTaglinePrompt, parseTaglineCompletion } from "../lib/ai";
import { OnboardingWebsiteDetails } from "../types";
import { debounce } from "lodash";

@Component
export default class OnboardingTagline extends Vue {
  rewriteInProgress = false;
  output: { tagline: string } = {
    tagline: ""
  };

  get websiteDetails() {
    return onboardingStore.websiteDetails;
  }

  async rewriteTagline(websiteDetails: OnboardingWebsiteDetails) {
    if (this.rewriteInProgress) return;

    if (!websiteDetails.websiteDescription || !websiteDetails.websiteName) {
      return;
    }

    this.rewriteInProgress = true;
    const prompt = getTaglinePrompt(websiteDetails);

    try {
      const response = await parseTaglineCompletion(prompt);
      this.output.tagline = response?.tagline || "";
    } catch (error) {
      console.error("Error rewriting tagline:", error);
    } finally {
      this.rewriteInProgress = false;
    }
  }

  private deboncedRewriteTagline = debounce(
    (websiteDetails: OnboardingWebsiteDetails) => {
      this.rewriteTagline(websiteDetails);
    },
    500
  ); // Adjust debounce delay (300ms in this case)

  @Watch("websiteDetails", { immediate: true, deep: true })
  onWebsiteDetailsChange(newValue: OnboardingWebsiteDetails) {
    if (newValue) {
      this.deboncedRewriteTagline(newValue);
    }
  }
}
