
import Vue from "vue";
import { Component } from "vue-property-decorator";
import OnboardingLayout from "../components/OnboardingLayout.vue";
import { routesOnboarding, PATHS } from "../router";
import Stepper from "../components/Stepper.vue";
import BottomNav from "../components/BottomNav.vue";
import OnboardingStartForm from "../components/OnboardingStartForm.vue";
import LogoBeta from "../components/LogoBeta.vue";
import { onboardingStore } from "@/store/store";
import OnboardingTagline from "../components/OnboardingTagline.vue";
import OnboardingFeedback from "../components/OnboardingFeedback.vue";

@Component({
  components: {
    OnboardingLayout,
    Stepper,
    BottomNav,
    OnboardingStartForm,
    LogoBeta,
    OnboardingTagline,
    OnboardingFeedback
  }
})
export default class OnboardingStart extends Vue {
  routesOnboarding = routesOnboarding;

  get websiteDetails() {
    return onboardingStore.websiteDetails;
  }

  get pathName() {
    return this.$route.name?.toLowerCase() || "";
  }

  get isWritingStyleRoute() {
    return this.$route.path === PATHS.WRITING_STYLE;
  }

  get isFormValid() {
    const routePath = this.$route.path;

    if (routePath === PATHS.START) {
      return (
        this.websiteDetails.websiteName.length > 0 &&
        this.websiteDetails.websiteDescription.length > 0
      );
    }

    if (routePath === PATHS.CATEGORY) {
      return this.websiteDetails.websiteCategory.length > 0;
    }

    if (routePath === PATHS.GOALS) {
      return this.websiteDetails.websiteGoals.length > 0;
    }

    if (routePath === PATHS.WRITING_STYLE) {
      return this.websiteDetails.websiteWritingStyle.length > 0;
    }

    return false;
  }
}
