import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import OnboardingStart from "../views/OnboardingStart.vue";
import OnboardingSelectType from "../views/OnboardingSelectType.vue";
import OnboardingBarebonesStyleguide from "../views/barebones/OnboardingBarebonesStyleguide.vue";
import OnboardingComplete from "../views/OnboardingComplete.vue";
import OnboardingTemplateLibrary from "../views/template/OnboardingTemplateLibrary.vue";
import OnboardingDesignProviderImport from "../views/OnboardingDesignProviderImport.vue";
import OnboardingDesignProviderHomepage from "../views/OnboardingDesignProviderHomepage.vue";
import OnboardingDesignProviderPages from "../views/OnboardingDesignProviderPages.vue";
import OnboardingBarebonesSitemap from "../views/barebones/OnboardingBarebonesSitemap.vue";

Vue.use(VueRouter);

export const PATHS = {
  // Website
  START: "/start",
  CATEGORY: "/category",
  GOALS: "/goals",
  WRITING_STYLE: "/writing-style",
  // Customization
  CUSTOMIZATION_SELECT: "/customization",
  CUSTOMIZATION_NEXT: "/#", // Placeholder
  // Barebones Builder
  BAREBONES: "/barebones",
  BAREBONES_IMPORT: "/barebones/import",
  BAREBONES_STYLEGUIDE: "/barebones/styleguide",
  BAREBONES_SITEMAP: "/barebones/sitemap",
  BAREBONES_HOMEPAGE: "/barebones/homepage",
  BAREBONES_PAGES: "/barebones/pages",
  BAREBONES_COMPLETED: "/barebones/completed",

  // Template
  TEMPLATE: "/templates",
  TEMPLATE_IMPORT: "/templates/import",
  TEMPLATE_HOMEPAGE: "/template/homepage",
  TEMPLATE_PAGES: "/template/pages",
  TEMPLATE_COMPLETED: "/template/completed",

  // Completed
  COMPLETED: "/completed"
};

const homeRoute: RouteConfig = { path: "/", redirect: "/start" };

const barebonesRoute: RouteConfig = {
  path: "/barebones",
  redirect: "/barebones/import"
};

export const routesOnboarding: RouteConfig[] = [
  {
    path: PATHS.START,
    components: {
      default: OnboardingStart
    },
    name: "Start",
    meta: {
      title: "Start"
    }
  },
  {
    path: PATHS.CATEGORY,
    components: {
      default: OnboardingStart
    },
    name: "Category",
    meta: {
      title: "Category"
    }
  },
  {
    path: PATHS.GOALS,
    component: OnboardingStart,
    name: "Goals",
    meta: {
      title: "Goals"
    }
  },
  {
    path: PATHS.WRITING_STYLE,
    component: OnboardingStart,
    name: "WritingStyle",
    meta: {
      title: "Writing Style",
      nextPath: PATHS.CUSTOMIZATION_SELECT
    }
  }
];

const completedRoute: RouteConfig = {
  path: PATHS.COMPLETED,
  component: OnboardingComplete,
  name: "Completed",
  meta: {
    title: "Finish"
  }
};

export const routesCustomization: RouteConfig[] = [
  {
    path: PATHS.CUSTOMIZATION_SELECT,
    component: OnboardingSelectType,
    name: "CustomizationSelect",
    meta: {
      title: "Select"
    }
  },
  {
    path: PATHS.CUSTOMIZATION_NEXT,
    component: OnboardingSelectType,
    name: "CustomizationNext",
    meta: {
      title: "Customization"
    }
  }
];

export const routesBarebones: RouteConfig[] = [
  {
    path: PATHS.BAREBONES_SITEMAP,
    component: OnboardingBarebonesSitemap,
    name: "BarebonesSitemap",
    meta: {
      title: "Sitemap"
    },
    props: () => ({
      routes: routesBarebones
    })
  },

  {
    path: PATHS.BAREBONES_STYLEGUIDE,
    component: OnboardingBarebonesStyleguide,
    name: "BarebonesStyleguide",
    meta: {
      title: "Style"
    },
    props: () => ({
      routes: routesBarebones
    })
  },
  {
    path: PATHS.BAREBONES_HOMEPAGE,
    component: OnboardingDesignProviderHomepage,
    name: "BarebonesHomepage",
    meta: {
      title: "Homepage"
    },
    props: () => ({
      routes: routesBarebones
    })
  },
  {
    path: PATHS.BAREBONES_PAGES,
    component: OnboardingDesignProviderPages,
    name: "BarebonesPages",
    meta: {
      title: "Pages"
    },
    props: () => ({
      routes: routesBarebones
    })
  },
  {
    path: PATHS.BAREBONES_COMPLETED,
    redirect: PATHS.COMPLETED,
    meta: {
      title: "Finish"
    }
  }
];

export const routesTemplates: RouteConfig[] = [
  {
    path: PATHS.TEMPLATE,
    component: OnboardingTemplateLibrary,
    name: "TemplateLibrary",
    meta: {
      title: "Library"
    }
  },
  {
    path: PATHS.TEMPLATE_IMPORT,
    components: {
      default: OnboardingDesignProviderImport
    },
    name: "TemplateImport",
    meta: {
      title: "Import"
    }
  },
  {
    path: PATHS.TEMPLATE_HOMEPAGE,
    component: OnboardingDesignProviderHomepage,
    name: "TemplateHomepage",
    meta: {
      title: "Homepage"
    },
    props: () => ({
      routes: routesTemplates
    })
  },
  {
    path: PATHS.TEMPLATE_PAGES,
    component: OnboardingDesignProviderPages,
    name: "TemplatePages",
    meta: {
      title: "Pages"
    },
    props: () => ({
      routes: routesTemplates
    })
  },
  {
    path: PATHS.TEMPLATE_COMPLETED,
    redirect: PATHS.COMPLETED,
    meta: {
      title: "Finish"
    }
  }
];

const router = new VueRouter({
  mode: "hash",
  routes: [
    homeRoute,
    completedRoute,
    ...routesOnboarding,
    ...routesCustomization,
    barebonesRoute,
    ...routesBarebones,
    ...routesTemplates
  ]
});

export default router;
