
import Vue from "vue";
import { Component, Prop, Watch } from "vue-property-decorator";
import CheckboxInput from "./CheckboxInput.vue";
import { MultipleChoiceOption } from "../types";

@Component({
  components: {
    CheckboxInput
  }
})
export default class MultipleChoiceSection extends Vue {
  internalValue: MultipleChoiceOption[] = [];

  @Prop({ type: Array, required: true })
  value!: MultipleChoiceOption[];

  @Prop()
  title?: string;

  @Prop({ type: Array })
  selected!: MultipleChoiceOption[];

  @Prop()
  description?: string;

  @Prop()
  note?: string;

  @Prop()
  limit?: number;

  get isLimitReached() {
    return this.limit && this.internalValue.length >= this.limit;
  }

  update() {
    if (this.limit && this.internalValue.length > this.limit) {
      this.internalValue = this.internalValue.slice(0, this.limit);
    }

    this.internalValue = this.internalValue.map(option => ({
      ...option,
      selected: true
    }));

    this.$emit("update", this.internalValue);
  }

  @Watch("value", { immediate: true, deep: true })
  onValueChange(newValue: MultipleChoiceOption[]) {
    this.internalValue = newValue.filter(option => option.selected);
  }
}
