
import Vue from "vue";
import { Component, Prop, Watch, Ref } from "vue-property-decorator";
import { onboardingStore, configStore } from "@/store/store";
import { BORDER_RADIUS_VALUES, OnboardingWebsiteUI } from "../types";
import { websiteColorPalettesToGlobalColorsArray } from "../lib/color";
import { loadDocumentFromWordPressToOnboardingPage } from "../lib/pages";
import BrowserUIPreview from "./BrowserUIPreview.vue";
import iframeManager from "../lib/iframeManager";

@Component({
  components: {
    BrowserUIPreview
  }
})
export default class OnboardingFoundationStyleguide extends Vue {
  @Prop()
  pageId?: number;

  @Prop({
    default: false
  })
  loading!: boolean;

  @Ref() readonly iframe!: HTMLIFrameElement | null;

  get iframeUrl() {
    return `/?breakdance=builder&id=${this.pageId}&onboarding=true`;
  }

  get showSkeleton() {
    return !this.pageId || !this.loading;
  }

  get palettes() {
    return onboardingStore.websiteUI.colorPalettes;
  }

  get buttons() {
    return onboardingStore.websiteUI.buttons;
  }

  get images() {
    return onboardingStore.websiteUI.images;
  }

  get typography() {
    return onboardingStore.websiteUI.typography;
  }

  get previewBreakpointId() {
    return onboardingStore.previewBreakpointId;
  }

  get breakpoints() {
    return configStore.breakpoints;
  }

  get websiteUI() {
    return onboardingStore.websiteUI;
  }

  get selectedHomepageSectionsIdsToRemove() {
    return onboardingStore.homepageSectionIdsToRemove;
  }

  onIframeLoaded() {
    const iframe = this.iframe as HTMLIFrameElement;
    iframeManager.set(iframe);
  }

  mounted() {
    this.$nextTick(() => {
      this.onIframeLoaded();
    });
  }

  @Watch("pageId", { immediate: true })
  async onPageIdChange(newVal: number) {
    if (!newVal) {
      return;
    }

    const response = await loadDocumentFromWordPressToOnboardingPage(newVal);

    if (!response?.document.tree) {
      return;
    }

    onboardingStore.updateCurrentTree(response?.document.tree.root.children);
  }

  @Watch("previewBreakpointId", { immediate: true })
  onPreviewBreakpointIdChange(newVal: string) {
    console.log("Preview breakpoint changed:", newVal);
    if (!newVal) {
      return;
    }
    const breakpoint = this.breakpoints.find(
      breakpoint => breakpoint.id === newVal
    );

    if (!breakpoint) {
      return;
    }

    iframeManager.sendRequest({
      payload: {
        action: "updateBreakpoint",
        data: { breakpoint }
      }
    });
  }

  @Watch("websiteUI", { deep: true, immediate: true })
  onWebsiteUIChange(newVal: OnboardingWebsiteUI) {
    if (!newVal) {
      return;
    }
    const globalColors = websiteColorPalettesToGlobalColorsArray(
      newVal.colorPalettes
    );
    const paths = [
      {
        path: "settings.colors",
        value: {
          palette: {
            colors: globalColors
          },
          brand: `var(--${newVal.colorPalettes.primary?.base.cssVariableName})`,
          text: `var(--${newVal.colorPalettes.neutral?.base.cssVariableName})`,
          headings: `var(--${newVal.colorPalettes.neutral?.base.cssVariableName})`,
          background: `var(--${newVal.colorPalettes.primary?.scale[100].cssVariableName})`
        }
      },
      {
        path: "settings.buttons",
        value: {
          primary: {
            corners: "custom",
            corner_radius: BORDER_RADIUS_VALUES[newVal.buttons?.radius],
            background: `var(--${newVal.colorPalettes.primary?.base.cssVariableName})`,
            background_hover: `var(--${newVal.colorPalettes.primary?.scale[600].cssVariableName})`,
            typography: {
              color: `var(--${newVal.colorPalettes.primary?.text.cssVariableName})`
            }
          },
          secondary: {
            corners: "custom",
            corner_radius: BORDER_RADIUS_VALUES[newVal.buttons?.radius],
            outline: "true",
            color: `var(--${newVal.colorPalettes.neutral?.scale[300].cssVariableName})`,
            typography: {
              color: `var(--${newVal.colorPalettes.neutral?.scale[900].cssVariableName})`
            }
          }
        }
      },
      {
        path: "settings.forms",
        value: {
          fields: {
            borders: {
              radius: {
                all: BORDER_RADIUS_VALUES[newVal.buttons?.radius]
              }
            }
          }
        }
      },
      {
        path: "settings.typography",
        value: {
          heading_font: newVal.typography.headings?.slug,
          body_font: newVal.typography.body?.slug
        }
      }
    ];

    paths.forEach(({ path, value }) => {
      iframeManager.sendRequest({
        payload: {
          action: "updateGlobalSettingsPerPath",
          data: { path, value }
        }
      });
    });
  }
}
