
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import Logo from "../components/Logo.vue";
import OnboardingLoader from "../components/OnboardingLoader.vue";
import Icon from "@/components/UI/Icon.vue";
import { Sitemap } from "../types";
import { onboardingStore } from "@/store/store-accessor";

@Component({
  components: {
    Logo,
    OnboardingLoader,
    Icon
  }
})
export default class OnboardingSitemap extends Vue {
  @Prop()
  sitemap!: Sitemap;

  @Prop({
    default: false
  })
  loading!: boolean;

  removePage(index: number) {
    onboardingStore.removePageFromSitemapAction(index);
  }
}
