
import Vue from "vue";
import { Component } from "vue-property-decorator";
import OnboardingLayout from "../components/OnboardingLayout.vue";
import { routesCustomization, PATHS } from "../router";
import Stepper from "../components/Stepper.vue";
import BottomNav from "../components/BottomNav.vue";
import CardLink from "../components/CardLink.vue";
import LogoBeta from "../components/LogoBeta.vue";
import { onboardingStore } from "@/store/store";
import { pipe } from "fp-ts/function";
import { fold } from "fp-ts/Either";
import { makeWordPressAjaxRequest } from "@/lib/wordpress/ajax";
import { DesignLibraryData, DesignLibraryDataV } from "@/types/design-library";
import OnboardingLoader from "../components/OnboardingLoader.vue";
import { logEvent } from "@/util/tracking";
import { getOnboardingSettings, saveOnboardingSettings } from "../lib/settings";
import OnboardingFeedback from "../components/OnboardingFeedback.vue";

@Component({
  components: {
    OnboardingLayout,
    Stepper,
    BottomNav,
    CardLink,
    LogoBeta,
    OnboardingLoader,
    OnboardingFeedback
  }
})
export default class OnboardingSelectType extends Vue {
  pathBarebones = PATHS.BAREBONES_SITEMAP;
  pathTemplate = PATHS.TEMPLATE;
  routesCustomization = routesCustomization;
  isLoading = true;

  get designLibraryData(): DesignLibraryData {
    return onboardingStore.designLibrary.data;
  }

  get websiteDetails() {
    return onboardingStore.websiteDetails;
  }

  async importProviders() {
    return pipe(
      await makeWordPressAjaxRequest({
        action: "breakdance_get_design_library_data",
        decoder: DesignLibraryDataV
      }),
      fold(
        () => {
          this.isLoading = false;
        },
        data => {
          onboardingStore.updateDesignLibraryData(data);
          this.isLoading = false;
        }
      )
    );
  }

  async setBarebonesProviderAndRedirect() {
    logEvent("Onboarding", {
      action: "onboarding_app_select_website_type",
      type: "barebones"
    });
    onboardingStore.setBarebonesDesignProvider();
    await saveOnboardingSettings();
    this.$router.push(this.pathBarebones);
  }

  goToTemplatesGallery() {
    logEvent("Onboarding", {
      action: "onboarding_app_select_website_type",
      type: "template"
    });

    this.$router.push(this.pathTemplate);
  }

  async mounted() {
    await getOnboardingSettings();
    await this.importProviders();

    logEvent("Onboarding", {
      action: "onboarding_app_website_details",
      details: {
        description: this.websiteDetails.websiteDescription,
        name: this.websiteDetails.websiteName,
        category: this.websiteDetails.websiteCategory.map(
          category => category.label
        ),
        goals: this.websiteDetails.websiteGoals.map(goal => goal.label),
        personality: this.websiteDetails.websiteWritingStyle.map(
          personality => personality.label
        )
      }
    });
  }
}
