
import Vue from "vue";

import { Component, Watch } from "vue-property-decorator";
import TextInput from "../components/TextInput.vue";
import { PATHS } from "../router";
import CheckboxInput from "../components/CheckboxInput.vue";
import Button from "../components/Button.vue";
import { onboardingStore } from "@/store/store";
import { fireConfetti } from "../lib/confetti";
import { OnboardingGeneratedPage } from "../types";
import { saveOnboardingSettings } from "../lib/settings";

import {
  WebsiteCategoriesV,
  WebsiteGoalsV,
  WebsiteWritingStyleV,
  MultipleChoiceOption
} from "../types";
import MultipleChoiceSection from "./MultipleChoiceSection.vue";

@Component({
  components: {
    TextInput,
    CheckboxInput,
    Button,
    MultipleChoiceSection
  }
})
export default class OnboardingStartForm extends Vue {
  websiteExamples: any = [
    {
      businessName: "Sage and Bloom",
      businessDescription:
        "Sage & Bloom is a local flower shop that offers custom bouquets, event arrangements, and same-day delivery, letting you bring beauty to any moment with ease."
    },
    {
      businessName: "Oakwood Bistro",
      businessDescription:
        "Oakwood Bistro is a neighborhood restaurant that serves seasonal, locally sourced dishes in a cozy setting, with online reservations and takeout available."
    },
    {
      businessName: "Lume Jewelry",
      businessDescription:
        "Lume Jewelry is a minimalist e-commerce brand that sells handcrafted rings, necklaces, and earrings, making it easy to shop timeless pieces online."
    },
    {
      businessName: "Willa Grant",
      businessDescription:
        "Willa Grant is a personal brand site for a freelance designer, showcasing her portfolio, client testimonials, and booking availability in one clean, modern layout."
    },
    {
      businessName: "RiverRun Festival",
      businessDescription:
        "RiverRun Festival is an annual music and arts event that features local talent, food trucks, and family-friendly activities with tickets, schedules, and info all online."
    },
    {
      businessName: "Nest Interiors",
      businessDescription:
        "Nest Interiors is a boutique interior design studio that helps homeowners transform their spaces with personalized, modern designs and easy online consultations."
    },
    {
      businessName: "The Daily Crumb",
      businessDescription:
        "The Daily Crumb is a cozy bakery and caf that offers fresh pastries, artisanal coffee, and online ordering for pickup and delivery."
    },
    {
      businessName: "TrailNotes",
      businessDescription:
        "TrailNotes is a personal blog that shares hiking stories, trail guides, and gear reviews to inspire your next outdoor adventure."
    },
    {
      businessName: "BrightFuture Fund",
      businessDescription:
        "BrightFuture Fund is a non-profit organization dedicated to expanding educational access for underserved communities through scholarships and mentorship programs."
    },
    {
      businessName: "LaunchWell",
      businessDescription:
        "LaunchWell is a startup that offers easy-to-use tools for launching and managing product waitlists, landing pages, and user feedback collection."
    }
  ];

  get isHomepageSet() {
    return onboardingStore.generatedPages.homepage;
  }

  get websiteDetails() {
    return onboardingStore.websiteDetails;
  }

  get categories() {
    return WebsiteCategoriesV.map(category => ({
      ...category,
      selected: this.websiteDetails.websiteCategory.some(
        selectedCategory => selectedCategory.id === category.id
      )
    }));
  }

  get goals() {
    return WebsiteGoalsV.map(goal => ({
      ...goal,
      selected: this.websiteDetails.websiteGoals.some(
        selectedGoal => selectedGoal.id === goal.id
      )
    }));
  }

  get writingStyles() {
    return WebsiteWritingStyleV.map(style => ({
      ...style,
      selected: this.websiteDetails.websiteWritingStyle.some(
        selectedWritingStyle => selectedWritingStyle.id === style.id
      )
    }));
  }

  get isCategoryPath() {
    return this.$route.path === PATHS.CATEGORY;
  }

  get isStartPath() {
    return this.$route.path === PATHS.START;
  }

  get isGoalsPath() {
    return this.$route.path === PATHS.GOALS;
  }

  get isWritingStylePath() {
    return this.$route.path === PATHS.WRITING_STYLE;
  }

  generateExample() {
    let randomIndex;
    do {
      randomIndex = Math.floor(Math.random() * this.websiteExamples.length);
    } while (
      this.websiteDetails.websiteName ===
        this.websiteExamples[randomIndex].businessName &&
      this.websiteDetails.websiteDescription ===
        this.websiteExamples[randomIndex].businessDescription &&
      this.websiteExamples.length > 1
    );
    const example = this.websiteExamples[randomIndex];
    this.updateName(example.businessName);
    this.updateDescription(example.businessDescription);
    fireConfetti();
  }

  updateName(name: string) {
    onboardingStore.updateWebsiteDetails({ websiteName: name });
  }

  updateDescription(description: string) {
    onboardingStore.updateWebsiteDetails({ websiteDescription: description });
  }

  updateCategories(selectedOptions: MultipleChoiceOption[]) {
    onboardingStore.updateWebsiteDetails({ websiteCategory: selectedOptions });
  }

  updateGoals(selectedOptions: MultipleChoiceOption[]) {
    onboardingStore.updateWebsiteDetails({ websiteGoals: selectedOptions });
  }

  updateWritingStyle(selectedOptions: MultipleChoiceOption[]) {
    onboardingStore.updateWebsiteDetails({
      websiteWritingStyle: selectedOptions
    });
  }

  @Watch("isHomepageSet", { immediate: true })
  onHomepageSetChange(newHomepageSet: OnboardingGeneratedPage) {
    if (newHomepageSet) {
      this.$root.$emit("toast", {
        message: "Looks like you are already stared onboarding",
        duration: 9999,
        callback: () => this.resetOnboarding(),
        callbackText: "Restart"
      });
    }
  }

  async resetOnboarding() {
    onboardingStore.resetOnboardingData();
    await saveOnboardingSettings();
    if (this.$route.path !== "/start") {
      this.$router.push("/start");
    }
  }
}
