
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { MultipleChoiceOption } from "../types";

@Component({
  model: {
    prop: "modelValue",
    event: "input"
  }
})
export default class CheckboxInput extends Vue {
  @Prop()
  selectedValue!: MultipleChoiceOption;

  @Prop()
  id?: string;

  @Prop()
  modelValue!: MultipleChoiceOption[];

  @Prop()
  disabled?: boolean;

  get isChecked() {
    return this.modelValue.some(option => option.id === this.selectedValue.id);
  }

  update(event: Event) {
    const checked = (event.target as HTMLInputElement).checked;
    let newValue = [...this.modelValue];

    if (checked) {
      newValue.push(this.selectedValue);
    } else {
      newValue = newValue.filter(option => option.id !== this.selectedValue.id);
    }

    this.$emit("input", newValue);
  }
}
