
import Vue from "vue";
import Logo from "./Logo.vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";

@Component({
  components: {
    Logo
  }
})
export default class OnboardingLayout extends Vue {
  @Prop()
  background?: string;

  get backgroundCss() {
    return {
      backgroundColor: `var(--${this.background})`
    };
  }
}
