
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import Button from "./Button.vue";
import { logEvent } from "@/util/tracking";
import { fireConfetti } from "../lib/confetti";
import Icon from "@/components/UI/Icon.vue";

@Component({
  components: {
    Button,
    Icon
  }
})
export default class OnboardingFeedback extends Vue {
  showFeedback = false;
  feedbackTextArea: HTMLTextAreaElement | null = null;
  feedback = "";
  rating: number | null = null;

  @Prop({ default: false })
  floating!: boolean;

  get isValidFeedback() {
    return this.feedback.length > 0 || (this.rating && this.rating > 0);
  }

  toggle() {
    this.showFeedback = !this.showFeedback;

    this.$nextTick(() => {
      const textarea = this.$refs.feedbackTextarea as HTMLTextAreaElement;
      if (textarea) {
        textarea.focus();
      }
    });
  }

  sendFeedback() {
    if (this.feedback.length > 0) {
      logEvent("Onboarding", {
        action: "onboarding_app_leave_feedback",
        feedback: this.feedback,
        rating: this.rating
      });
      this.feedback = "";
      this.rating = null;
      this.showFeedback = false;
      fireConfetti();
      this.$root.$emit("toast", {
        message: "Thank you for your feedback!",
        duration: 3000
      });
    }
  }
}
