
import Vue from "vue";
import Component from "vue-class-component";
import DropdownSection from "./DropdownSection.vue";
import TypographyPicker from "./TypographyPicker.vue";
import { logEvent } from "@/util/tracking";
import { onboardingStore } from "@/store/store";
import { Font } from "@/types/fonts";

@Component({
  components: {
    DropdownSection,
    TypographyPicker
  }
})
export default class ManageTypography extends Vue {
  get typography() {
    return onboardingStore.websiteUI.typography;
  }

  get headingFont() {
    return onboardingStore.websiteUI.typography.headings;
  }
  get bodyFont() {
    return onboardingStore.websiteUI.typography.body;
  }

  updateHeadings(value: Font) {
    logEvent("Onboarding", {
      action: "onboarding_app_ui_interaction",
      type: "manage_typography_headings"
    });

    onboardingStore.updateTypography({
      headings: value
    });
  }
  updateBody(value: Font) {
    logEvent("Onboarding", {
      action: "onboarding_app_ui_interaction",
      type: "manage_typography_headings"
    });

    onboardingStore.updateTypography({
      body: value
    });
  }
}
