
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import OnboardingLayout from "../../components/OnboardingLayout.vue";
import Stepper from "../../components/Stepper.vue";
import BottomNav from "../../components/BottomNav.vue";
import Button from "../../components/Button.vue";
import { onboardingStore } from "@/store/store";
import LogoBeta from "../../components/LogoBeta.vue";
import { RouteConfig } from "vue-router";
import OnboardingFeedback from "../../components/OnboardingFeedback.vue";
import OnboardingSitemap from "../../components/OnboardingSitemap.vue";
import Icon from "@/components/UI/Icon.vue";

import {
  convertPostsWithValidTreeToBarebonesSectionLibrary,
  getPostsWithValidTreeFromRemote
} from "../../lib/pages";
import { setBarebonesSectionLibrary } from "../../lib/barebonesLibrary";
import { generateSitemap } from "../../lib/ai";
import { Sitemap } from "../../types";
import { createPageInWP } from "../../lib/pages";

@Component({
  components: {
    OnboardingLayout,
    Stepper,
    Button,
    LogoBeta,
    BottomNav,
    OnboardingFeedback,
    OnboardingSitemap,
    Icon
  }
})
export default class OnboardingFoundationStyleguide extends Vue {
  @Prop()
  routes!: RouteConfig[];

  generatingSitemap = false;

  get sitemap(): Sitemap | null {
    return onboardingStore.sitemap;
  }

  async generateSitemap(): Promise<void> {
    this.generatingSitemap = true;
    getPostsWithValidTreeFromRemote().then(async posts => {
      if (!posts) {
        this.generatingSitemap = false;
        this.$root.$emit("toast", {
          message: "Failed to generate sitemap. Please try again later.",
          duration: 3000
        });
      } else {
        const barebonesSectionLibrary =
          convertPostsWithValidTreeToBarebonesSectionLibrary(posts);
        setBarebonesSectionLibrary(barebonesSectionLibrary);
      }

      const generatedSitemap = await generateSitemap();
      if (generatedSitemap) {
        onboardingStore.updateSitemap(generatedSitemap);
        this.$root.$emit("toast", {
          message: "Sitemap generated successfully!",
          duration: 3000
        });
      }
      this.generatingSitemap = false;
    });
  }

  async mounted() {
    if (this.sitemap) {
      return;
    }
    await this.generateSitemap();
  }

  async createPagesFromSitemap(): Promise<void> {
    const sitemap = this.sitemap ?? [];
    const pagesToCreate = sitemap.map((page: any) => ({
      title: page.title,
      sections: page.sections
    }));

    return Promise.resolve()
      .then(() =>
        Promise.all(pagesToCreate.map((page: any) => createPageInWP(page)))
      )
      .then((createdPages: any[]) => {
        if (createdPages.length === 0) {
          return;
        }
        onboardingStore.updateGeneratedPages({
          homepage: createdPages[0] ?? null,
          pages: createdPages.slice(1),
          toRemove: {
            homepageSectionIds: null,
            pageIds: null
          }
        });
        this.$root?.$emit("toast", {
          message: "Pages created successfully!",
          duration: 3000
        });
      })
      .catch(error => {
        console.error("Error creating pages:", error);
        this.$root?.$emit("toast", {
          message: "Failed to create pages. Please try again later.",
          duration: 3000
        });
      });
  }
}
