
import Vue from "vue";
import { Component } from "vue-property-decorator";
import { uiStore, configStore } from "@/store/store";
import { Breakpoint } from "@/types/breakpoints";
import { onboardingStore } from "@/store/store";
import Icon from "@/components/UI/Icon.vue";

@Component({
  components: {
    Icon
  }
})
export default class ToggleBreakpoints extends Vue {
  get currentBreakpoint() {
    return onboardingStore.previewBreakpointId;
  }

  get currentBreakpointFromStore() {
    return uiStore.currentBreakpoint;
  }

  get breakpoints() {
    return configStore.breakpoints;
  }

  setBreakpoint(breakpointId: Breakpoint["id"]) {
    onboardingStore.setPreviewBreakpointId(breakpointId);
  }
}
