
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { TreeNode } from "@/types/tree";
import { RemoteDesignProviderPost } from "../types";
import Icon from "@/components/UI/Icon.vue";
import iframeManager from "../lib/iframeManager";
import TextInput from "../components/TextInput.vue";
import Button from "../components/Button.vue";
import { groupedGlobalColors } from "@/subapps/onboarding/lib/color";
import { globalStore, onboardingStore } from "@/store/store-accessor";
import NodeOnboardingPaletteButton from "@/components/Canvas/OverlaidUI/Onboarding/NodeOnboardingPaletteButton.vue";
import DropdownSection from "./DropdownSection.vue";
import { logEvent } from "@/util/tracking";

@Component({
  components: {
    Icon,
    TextInput,
    Button,
    NodeOnboardingPaletteButton,
    DropdownSection
  }
})
export default class OnboardingInsertSectionSelect extends Vue {
  isLoading = false;
  isDropdownOpen = false;
  filter = "";
  isDropdownActive = false;
  sectionPromptValue = "";

  @Prop()
  section!: TreeNode;

  @Prop()
  designProviderPosts!: RemoteDesignProviderPost[];

  @Prop()
  isActive!: boolean;

  created() {
    this.sectionPromptValue = this.hasPromptInfo
      ? this.section.data.properties.meta.promptInfo.sectionDescription
      : "";
  }

  get isBarebones() {
    return (
      onboardingStore.designLibrary.selectedProvider &&
      onboardingStore.designLibrary.selectedProvider.name === "Barebones"
    );
  }

  get groupedGlobalColors() {
    return groupedGlobalColors(globalStore.globalColors);
  }

  get hasPromptInfo() {
    return (
      this.section.data.properties &&
      this.section.data.properties.meta &&
      this.section.data.properties.meta.promptInfo &&
      this.section.data.properties.meta.promptInfo.onboarding
    );
  }

  get sectionTitle() {
    return this.hasPromptInfo &&
      this.section.data.properties.meta.promptInfo.onboarding.sectionType !==
        false
      ? this.section.data.properties.meta.promptInfo.onboarding.sectionType
      : "Section";
  }

  async handleSectionPaste(providerSection: TreeNode) {
    providerSection.data.properties.meta =
      this.section.data.properties.meta || {};

    logEvent("Onboarding", {
      action: "onboarding_app_ui_interaction",
      type: "sidebar_import_section_paste"
    });

    await iframeManager.sendRequest({
      payload: {
        action: "importSectionFromOnboarding",
        data: {
          sectionTree: providerSection,
          activeElementId: this.section.id
        }
      }
    });
  }

  toggle() {
    this.isDropdownOpen = !this.isDropdownOpen;
    if (this.isDropdownOpen) {
      iframeManager.sendRequest({
        payload: {
          action: "onboardingActivateElement",
          data: {
            elementId: this.section.id
          }
        }
      });
    }
  }

  async updateSectionPromptAndRewrite() {
    this.isLoading = true;

    logEvent("Onboarding", {
      action: "onboarding_app_ui_interaction",
      type: "sidebar_update_section_prompt_and_rewrite"
    });

    await iframeManager.sendRequest({
      payload: {
        action: "updateSectionPrompt",
        data: {
          elementId: this.section.id,
          sectionDescription: this.sectionPromptValue
        }
      }
    });
    this.isLoading = false;
  }

  shuffle() {
    logEvent("Onboarding", {
      action: "onboarding_app_ui_interaction",
      type: "sidebar_shuffle_section"
    });

    iframeManager.sendRequest({
      payload: {
        action: "shuffleSection",
        data: {
          elementId: this.section.id
        }
      }
    });
  }

  get filteredPosts() {
    if (this.filter.length === 0) {
      if (
        this.hasPromptInfo &&
        this.section.data.properties.meta.promptInfo.onboarding.sectionType
      ) {
        const filtered = this.designProviderPosts.filter(
          post =>
            post.title ===
            this.section.data.properties.meta.promptInfo.onboarding.sectionType
        );
        return filtered.length > 0 ? filtered : this.designProviderPosts;
      }
      return this.designProviderPosts;
    }

    const lowerFilter = this.filter.toLowerCase();
    return this.designProviderPosts.filter(post =>
      post.title.toLowerCase().includes(lowerFilter)
    );
  }
}
